$base-yellow: rgb(249, 238, 178);
$base-pink: #f7d8e5;
$base-blue: #7bd9e6;
$font-color:rgb(54, 54, 54);


.main-about{

    .box{
        -webkit-box-shadow: 3px 3px 5px 6px #ccc;  /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
        -moz-box-shadow:    3px 3px 5px 6px #ccc;  /* Firefox 3.5 - 3.6 */
        box-shadow:         3px 3px 5px 6px #ccc;  /* Opera 10.5, IE 9, Firefox 4+, Chrome 6+, iOS 5 */
          
    }
    .cointainer2{
        .left-content{
            .left{
                padding:6% 2% 5% 2% !important;
            }
            .right{
                padding: 4% 10% 3% 10%;
            }
        }
        .right-content{
            .right{
                padding:6% 2% 5% 2% !important;
            }
            .left{
                padding: 4% 10% 3% 10%;
            }
        }
    }

h1{
    padding-top: 8% ;
    padding-bottom: 4%  ;
    background-color: rgb(25, 42, 56);
    color: white;
    margin: 0%;
}
h2{
    font-size: 3vw;
    font-family: "Copperplate", "Courier New", monospace;
    font-weight: 600 ;
    padding: 2%;
    background: rgb(255,255,254);
    background: linear-gradient(90deg, rgba(255,255,254,0.8578781854538691) 0%,   $base-pink 50%, rgba(255,255,254,0.8578781854538691) 100%);
    
    margin: 0%;
}

.right-content{
    color:$font-color !important; 
    text-align: center;
    margin: 3vw 1vw;
    height: 18vw;
    border-radius: 12px;
background: rgb(255,255,254);
background: linear-gradient(90deg, rgba(255,255,254,0.8578781854538691) 0%,   $base-yellow 100%);

   
    .left{
    //background-color: $base-blue;
    font-size: 2vw;
    font-weight: 500;
    font-family: "Copperplate" , monospace;
    padding: 8% 10% 7% 10%;
}
    .right{
        align-items: center;
        font-size: 2vw;
    font-weight: 500;
    font-family: "Brush Script MT", monospace;
    padding: 6% 2% 5% 2%;
    }
}
.left-content{
    color:$font-color !important;
    text-align: center;
    margin: 3vw 1vw;
    height: 18vw;
    border-radius: 12px;
    background: rgb(255,255,254);
    background: linear-gradient(90deg, $base-blue 0% , rgba(255,255,254,0.8578781854538691) 100% );
    
    .right{
    //background-color: $base-yellow;
    font-size: 2vw;
    font-weight: 500;
    font-family: "Copperplate", "Courier New", monospace;
    padding: 8% 10% 7% 10%;
    
}
    .left        {
        align-items: center;
        font-size: 2vw;
    font-weight: 500;
    font-family: "Brush Script MT", "Courier New", monospace;
    padding: 6% 2% 5% 2%;
    }
}


}

@media (max-width: 900px) {
    .cointainer{
        .right-content{
            .left{font-size: 3vw !important;
            padding: 18% 8% !important;}
        }
        .left-content{
            .right{font-size: 3vw !important;
            padding: 18% 8% !important;}
        }
    }

    h1{font-size: 4vw;
        padding-top:10vh !important;
        padding-bottom: 4% !important ;}

    .right-content{
        height: 40vw !important;
        .left{ font-size: 4vw !important;
            padding: 10% !important;
        }
        .right{ font-size: 4vw !important;
           }
       
    }
    .left-content{
        height: 40vw !important;
        .left{ font-size: 4vw !important;}
        .right{ font-size: 4vw !important;
            padding: 10% !important;}
       
    }

}