$base-yellow: rgb(249, 238, 178);
$base-pink: #f7d8e5;
$base-blue: #7bd9e6;

.abhi{
  position: fixed;
    top:  0;
    width: 100%;
    z-index: 100;
}
.nav{
  z-index: 1000;
}

.navbar{
    z-index: 1000;
    display:block !important;
    margin-right: 0px !important;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: rgba(255, 255, 255, 1);
    margin-top: 0px;
    padding-left: 85px;
    padding-right: 85px;
    .item{
      float:right !important ;
      font-family: monospace;
      font-size: 20px;

    }
    .navbar-brand{
      margin-left:0px !important;
      padding: 0px;
      margin: 0px;
      overflow: visible !important;
    }
    .logo{
      height: 50px;
      margin-top: 0vw;
      
      }


}
.navbar-toggler .collapsed .navbar .navbar-toggler-icon{
  background-image: url(https://i.stack.imgur.com/mSXoO.png)!important;
}

.sup{
    background-color: khaki;
}


/*Hide Scollbar*/
::-webkit-scrollbar { 
    display: none;  /* Chrome Safari */
    scrollbar-width: none !important;
    width: 0px; 
}

div {
  scrollbar-width: none !important; /* Firefox */ 
  -ms-overflow-style: none;  /* IE 10+ */
  overflow-y:scroll;
  overflow-x:hidden;
}
.element { overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;  }
/*Hide Scollbar*/

@media (max-width: 768px) {
  /* CSS that should be displayed if width is equal to or less than 800px goes here */
 .logo{
  width:auto;
  height: 20px;
 }
 .navbar{
  padding-left: 8px;
  padding-right: 8px;
 }

 .navbar{
  .item{
align-items: center;
text-align: center;
float: none !important;
font-size: 20px;
  }

}
}
.contact{
  position: sticky;
  top: 0;
  background-color: $base-yellow;
  padding-left: 70px;
  padding-top: 2px;
  padding-bottom: 2px;
  
  .email{
      display: inline-flex;
      float: left;
      p{
          padding-left: 1vw;
          margin-bottom: 0.2vw;
      }
    }
  .social{
      float: right;
      display: inline-flex;
      margin-right: 80px;
      a{
          color: #333;
      svg{
          margin-right: 1vw;
      }
  }
  }
 
}

